@charset "UTF-8";
/*
Exportar todos los estilos de tipo base para combinarlos en un solo archivo css
@Francisco Enríquez*/
/* BASE */
/*
Estilos para exportar fuentes de letra*/
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap");
/*
Estilos para mensaje de alerta
@Francisco Enríquez*/
.alerta {
  padding: 0.5rem;
  font-family: "Roboto", sans-serif;
  font-size: 2.2rem;
  text-align: center;
  color: white;
  font-weight: 600;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: red;
}
.alerta.exito {
  background-color: green;
}

/*
Estilos generales de todo el sitio web
@Francisco Enríquez*/
/*
Estilos generales del html
@Francisco Enríquez*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/*
Estilos generales del body
@Francisco Enríquez*/
body {
  font-size: 1.6rem;
  line-height: 1.8;
}

/*
Estilo de párrafos
@Francisco Enríquez*/
p {
  font-size: 2rem;
}

/*
Estilos para contenedores de secciones del sitio web
@Francisco Enríquez*/
.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

/*
Estilos para referencias
@Francisco Enríquez*/
a {
  text-decoration: none;
}
a:hover {
  color: rgb(0, 140, 255);
}

/*
Estilos para imágenes
@Francisco Enríquez*/
img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-weight: 300;
  text-align: center;
}

/*
Estilos para encabezados*/
h1, h2, h3, h4 {
  font-family: "Roboto", sans-serif;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

/*
Estilos para botones
@Francisco Enríquez*/
.boton {
  display: flex;
  justify-content: center;
  font-size: 2.5rem;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  padding: 2rem;
  border-radius: 1rem;
  background-color: rgb(182, 180, 180);
  border: none;
  margin-bottom: 2rem;
}
.boton:hover {
  background-color: #a9a7a7;
  color: white;
  cursor: pointer;
}

/*
Estilos para el menú de navegación
@Francisco Enríquez*/
.div_edicion {
  display: flex;
  flex-direction: row;
  margin: 1rem 3rem 0;
  border-bottom: 2px solid rgb(141, 141, 141);
  line-height: 0;
}
.div_edicion h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
}
.div_edicion .enlace-crear {
  text-decoration: none;
  display: flex;
  align-items: center;
  font-size: 2rem;
  color: black;
  background-color: rgb(182, 180, 180);
  padding: 2rem;
  border-radius: 1rem;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  margin: 0 0 1rem auto;
}
.div_edicion .enlace-crear:hover {
  background-color: #a9a7a7;
  cursor: pointer;
}

/*
Estilos para el menú de navegación
@Francisco Enríquez*/
.navegacion {
  margin: 1rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  column-gap: 0;
}
.navegacion button {
  width: 18rem;
  height: 7rem;
  background-color: gray;
  border: none;
  opacity: 0.65;
}
.navegacion button.actual {
  background-color: black;
}
.navegacion button:hover {
  cursor: pointer;
}
.navegacion button h3 {
  color: white;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

