.column-index {
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.card-shadow {
  width: 90rem;
  margin-bottom: -1rem;
}

.index {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  margin-top: 3rem;
}

.form-index {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 4rem;
  justify-content: center;
}

.form-index2 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4rem;
  justify-content: center;
}

.main-index {
  background-color: rgb(235, 235, 235);
}
.main-index h2 {
  font-family: "Roboto", sans-serif;
  font-weight: 600;
}

.h2-ordenar {
  font-size: 5vw;
}
@media (max-width: 575px) {
  .h2-ordenar {
    font-size: 6.5vw;
  }
}

/*
Estilos del apartado ordenar pedido
@Francisco Enríquez*/
.apartado2 {
  position: relative;
  /*
  Estilos del fondo del apartado de ordenar pedido
  @Francisco Enríquez*/
  /*
  Estilos del párrafo de ordenar pedido
  @Francisco Enríquez*/
  /*
  Estilos del fondo del título del encabezado de ordenar pedido
  @Francisco Enríquez*/
  /*
  Estilos de párrafo del apartado ordenar pedido
  @Francisco Enríquez*/
  /*
  Estilos del botón del apartado solicitar pedido
  @Francisco Enríquez*/
}
.apartado2 img {
  position: relative;
  display: inline-block;
  height: 100vh;
  object-fit: cover;
}
.apartado2 .texto-encima {
  position: absolute;
  color: white;
}
.apartado2 h2 {
  font-family: "Roboto", sans-serif;
}
.apartado2 p {
  top: 25%;
  font-family: "Roboto", sans-serif;
  margin: 0 20rem;
}
.apartado2 .boton-a2 {
  top: 80%;
}
@media (max-width: 575px) {
  .apartado2 img {
    height: 40vh;
  }
  .apartado2 p {
    margin: 0 2vw;
    font-size: 2vw;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .apartado2 img {
    height: 60vh;
  }
  .apartado2 p {
    margin: 0 2vw;
    font-size: 2.2vw;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .apartado2 img {
    height: 80vh;
  }
  .apartado2 p {
    margin: 0 2vw;
    font-size: 2.5vw;
  }
}

@media (max-width: 575px) {
  .boton {
    font-size: 2vw;
    margin-top: -2vh;
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .boton {
    font-size: 2.5vw;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .boton {
    font-size: 3vw;
  }
}

.nav-producto {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
}

/*
Estilos de la sección de comidas
@Francisco Enríquez*/
.contenido-comida {
  margin: 2rem 0 2rem auto;
  display: flex;
  flex-direction: row;
  /*
  Estilos para las imágenes de la sección de comidas
  @Francisco Enríquez*/
  /*
  Estilos para el resumen de la sección de comidas
  @Francisco Enríquez*/
}
.contenido-comida img {
  height: 35rem;
  width: 45rem;
  object-fit: cover;
}
.contenido-comida .resumen-comida {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 5rem;
  /*
  Estilos para el resumen de la sección de comidas
  @Francisco Enríquez*/
}
.contenido-comida .resumen-comida h1, .contenido-comida .resumen-comida p {
  color: black;
  font-family: "Roboto", sans-serif;
}

/*
Estilos de los botones de la sección de comidas
@Francisco Enríquez*/
.boton {
  width: 15rem;
}

/*
Estilos de los botones de la sección de comidas
@Francisco Enríquez*/
.parte-blanca {
  margin-bottom: -8rem;
}

@media (max-width: 575px) {
  .navegacion {
    display: grid;
    gap: 0.5vw;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
  }
}
@media (max-width: 575px) {
  .navegacion button {
    width: 25vw;
    height: 10vh;
  }
  .navegacion button h3 {
    font-size: 3vw;
  }
}

/*
Estilos del enlace para actualizar las comidas
@Francisco Enríquez*/
.enlace-actualizar {
  position: relative;
}
.enlace-actualizar img {
  width: 2.5rem;
  height: 2.5rem;
  object-fit: cover;
}

/*
Estilos del enlace para eliminar las comidas
@Francisco Enríquez*/
.enlace-eliminar {
  position: relative;
  /*
  Estilos del enlace para eliminar las comidas
  @Francisco Enríquez*/
}
.enlace-eliminar input[type=image] {
  width: 2.5rem;
  height: 2.5rem;
  object-fit: cover;
}

/*
Estilos de la sección para editar la comidas
@Francisco Enríquez*/
.edicion {
  display: flex;
  flex-direction: row;
  position: relative;
}

/*
Estilos del contenedor de la sección para editar la comidas
@Francisco Enríquez*/
.div_edicion .enlace-crear {
  font-size: 1vw;
}
@media (max-width: 575px) {
  .div_edicion .enlace-crear {
    font-size: 3.5vw;
  }
}

.buscador {
  display: flex;
  flex-direction: row;
  column-gap: 2rem;
}
.buscador .label-b {
  width: 60rem;
  height: 7rem;
  border: 1px solid black;
  border-radius: 5px;
  font-size: 3rem;
}
.buscador .b-buscador {
  height: auto;
  font-size: 2rem;
}

.articulosVenta {
  border: 1px solid black;
  border-radius: 5px;
  width: 100%;
  height: 70rem;
}

.ticket {
  border: 1px solid black;
  border-radius: 5px;
  width: 80rem;
  height: auto;
}
.ticket h2 {
  font-size: 4rem;
}

.puntoVenta {
  margin: 2rem 4rem 0;
  display: grid;
  grid-template-columns: repeat(2, 50% 50%);
  justify-items: center;
  gap: -20rem;
}